<template>
    <div class="page-index">
        <!-- <van-nav-bar            
            left-arrow
            title="我的好友"
            @click-left="onClickLeft"
        >    
        </van-nav-bar> -->
        <p class="title">我的好友(相互关注)</p>
        <div class="none-data" v-if="!data || !data.length">
            <img src="../../assets/images/pic_find.png" >
            <p>快去发现有趣的人~ Go~Go~Go~</p>
        </div>
        <section v-if="data">
            <van-list
                v-if="data"
                v-model="loading"
                :finished="finished"
                finished-text=""
                @load="onLoad"
            >
                <ul class="list">
                    <li  v-for="(item,key) in data" :key="key" >
                        <div class="left">
                            <div class="avatar" > <img :src="item.avatar" > <img class="isvip" src="../../assets/images/icon_isvip.png"  v-if="item.is_ident_icon == 1" > </div>
                            <div class="text">
                                <p class="username" >{{item.fusername}}</p>
                                <div class="flex flex-none"><p class="level" v-bind:class="{'isCarvip':item.is_carvip == 1}">{{item.level}}</p><p class="extcredits">{{item.extcredits2}}°</p></div>
                            </div>
                        </div>
                        <div class="right" @click="handleToWechatPage(item)">
                            <img src="../../assets/images/icon_mgs.png" >
                        </div>
                    </li>
                </ul>
            </van-list>
        </section>
    </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    data(){
        return {
            loading:false,
            finished:false,
            pageInfo:{
                page_size:10,
                page_index:0
            },
            tabs:3,
            data:''
        }
    },
    created(){

        this.init();
    },
    methods:{
        init(){
            document.title = '我的好友';
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            }); 
            this.getFollow();
        },
        addFollow(followuid){
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            });    
            this.api.addFollow({
                token:this.$store.state.userData,
                followuid:followuid
            }).then(res => {
                Toast.clear();
                if(res.err_code == 0){
                    this.pageInfo.page_index = 0;
                    this.getFollow();

                }else {
                    Toast(res.err_msg);
                }
            })
        },
        handleToWechatPage(item){
            let url = `${this.$store.state.indexUrl+'other/wechat?uid='+item.followuid+'&name='+item.fusername+'&'+this.$store.state.appParams}`,
                json = {url:url,share:'0',keyboard:'1'};
 
            window.location.href= `teldapp://newWebview?params=`+JSON.stringify(json)
          //  this.$router.push({path:'/other/wechat',query:{uid:item.uid,name:item.fusername}})

        },
        // 获取列表
        getFollow(){
            this.api.getFollow({
                token:this.$store.state.userData,
                type:this.tabs,
                page_size:this.pageInfo.page_size,
                page_index:this.pageInfo.page_index
            }).then(res => {
                Toast.clear();
                this.loading = false;
                if(res.err_code == 0){
                    
                    if(!res.data.follow_data.length || res.data.follow_data.length <  this.pageInfo.page_size){
                        this.finished = true;
                    }
                    if(this.pageInfo.page_index == 0){
                        this.data = [];
                    }
                    this.data = [...this.data,...res.data.follow_data]
                    this.pageInfo.page_index++;
                }
            })
        },
        onLoad(){
            this.getFollow();
        },
        onClickLeft(){
            this.$router.go(-1)
        }

    }
}
</script>
<style lang="less" scoped>
.page-index{
    .title{
        padding: 38px 15px;
        color: #CDCED3;
        font-size: 14px; 
    }
    .none-data{
        text-align: center;
        font-size: 14px;
        color: #888888;
        width: 130px;
        margin: 100px auto 0;
        img{
            width: 100%;
        }
    }
    .list{
        padding: 0 15px;
        li{
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 25px;
            .left{
                display: flex;
                
                .avatar{
                    width: 42px;
                    height: 42px;
                    margin-right: 13px;
                    position: relative;
                    img{
                        width: 100%;
                        height: 100%;
                        border-radius: 100% 100%;
                    }
                    .isvip{
                        position: absolute;
                        width: 14px;
                        height: 14px;
                        bottom: 0px;
                        right: 0px;
                    }
                }
                .username{
                    font-size: 16px;
                    color: #4E5578;
                    margin-bottom: 4px;   
                    flex: none;                 
                }
                .level{
                    color: #0EC6F1;
                    font-size: 10px;
                    padding: 0px 10px;
                    border-radius: 5px;
                    border: 1px solid #0EC6F1;
                    margin-right: 8px;
                    flex: none;
                }
                .isCarvip{
                    background-image: url('../../assets/images/icon_iscarvip.png');
                    background-repeat: no-repeat;
                    background-size: 11px 10px;
                    background-position-y: 46%;
                    background-position-x: 1px; 
                    padding-left: 14px;
                }
                .extcredits{
                    color: #0EC6F1;
                    padding: 1px 2px;
                    font-size: 10px;
                    border-radius: 5px;
                    border: 1px solid #0EC6F1;
                }

            }
            .right{
                img{
                    width: 20px;
                    height: 20px;
                }
            }

        }
    }
}
</style>